$themed_red: #ce4747;
$themed_yellow: #d89a28;
$themed_green: #65bc7b;
$themed_blue: #468ed4;

.theme-setter {
    position: fixed;
    right: 40px;
    display: flex;
    background-color: $white;
    box-shadow: 0 0 3px $gray;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding: 10px 0;
    z-index: 1;
    ul {
        list-style: none;
        display: flex;
        margin: 0 10px;
        padding: 0;
        li {
            min-width: 20px;
            cursor: pointer;
            min-height: 20px;
            line-height: 20px;
            font-size: 14px;
            margin: 0 5px;
            text-align: center;
            box-shadow: 0 0 3px $gray;
            user-select: none;
            &.active {
                border: 2px solid $black;
            }
            &.decrease {
                font-size: 10px;
            }
            &.increase {
                font-size: 15px;
            }
        }
    }
}

// Font Styles
.font-12 {
    .theme-font-size {
        font-size: 12px !important
    }
}
.font-13 {
    .theme-font-size {
        font-size: 13px !important
    }
}
.font-14 {
    .theme-font-size {
        font-size: 14px !important
    }
}
.font-15 {
    .theme-font-size {
        font-size: 15px !important
    }
}
.font-16 {
    .theme-font-size {
        font-size: 16px !important
    }
}

// Color Styles
.theme-red {
    .theme-back-color {
        background-color: $themed_red !important;
        border-color: $themed_red !important
    }
    .theme-color {
        color: $themed_red !important
    }
}
.theme-yellow {
    .theme-back-color {
        background-color: $themed_yellow !important;
        border-color: $themed_yellow !important
    }
    .theme-color {
        color: $themed_yellow !important
    }
}
.theme-green {
    .theme-back-color {
        background-color: $themed_green !important;
        border-color: $themed_green !important
    }
    .theme-color {
        color: $themed_green !important
    }
}
.theme-blue {
    .theme-back-color {
        background-color: $themed_blue !important;
        border-color: $themed_blue !important
    }
    .theme-color {
        color: $themed_blue !important
    }
}